import React from "react";
import { Link } from "react-scroll";
import {
    Box,
    Flex,
    Menu,
    MenuButton,
    MenuItem,
    MenuList,
    Text,
    useDisclosure,
} from "@chakra-ui/react";

function NavBar(props) {
    const mobileNav = useDisclosure();
    return (
        <Flex
            position={"sticky"}
            justifyContent={"space-between"}
            alignItems={"center"}
            top={0}
            Text={25}
            color={"#EEEEEE"}
            backgroundColor={"#393E46"}
            p={5}
            id="nav-menu"
        >
            <Flex>
                <Link smooth={true} duration={500} to="home">
                    <Text
                        fontWeight={"bold"}
                        fontSize={30}
                        cursor={"pointer"}
                        id="user-detail-name"
                    >
                        PRITAM
                    </Text>
                </Link>
            </Flex>
            <Flex display={["none",null,"flex"]} gap={19} alignItems={"center"}>
                <Text
                    fontWeight={"bold"}
                    cursor={"pointer"}
                    transition={"0.2s ease-in-out"}
                    _hover={{ color: "#00ADB5" }}
                >
                    <Link
                        smooth={true}
                        duration={500}
                        to="home"
                        className="nav-link home"
                    >
                        HOME
                    </Link>
                </Text>
                <Text
                    fontWeight={"bold"}
                    cursor={"pointer"}
                    transition={"0.2s ease-in-out"}
                    _hover={{ color: "#00ADB5" }}
                >
                <Link
                    smooth={true}
                    duration={500}
                    to="about"
                    className="nav-link about"
                >
                    ABOUT
                </Link>
                </Text>
                <Text
                    fontWeight={"bold"}
                    cursor={"pointer"}
                    transition={"0.2s ease-in-out"}
                    _hover={{ color: "#00ADB5" }}
                >
                <Link
                    smooth={true}
                    duration={500}
                    to="skills"
                    className="nav-link skills"
                >
                    SKILLS
                </Link>
                </Text>
                <Text
                    fontWeight={"bold"}
                    cursor={"pointer"}
                    transition={"0.2s ease-in-out"}
                    _hover={{ color: "#00ADB5" }}
                >
                <Link
                    smooth={true}
                    duration={500}
                    to="projects"
                    className="nav-link projects"
                >
                    PROJECTS
                </Link>
                </Text>
                <Text
                    fontWeight={"bold"}
                    cursor={"pointer"}
                    transition={"0.2s ease-in-out"}
                    _hover={{ color: "#00ADB5" }}
                >
                <Link
                    smooth={true}
                    duration={500}
                    to="contact"
                    className="nav-link contact"
                >
                    CONTACT
                </Link>
                </Text>
                <Text
                    fontWeight={"bold"}
                    cursor={"pointer"}
                    transition={"0.2s ease-in-out"}
                    _hover={{ color: "#00ADB5" }}
                >
                <a className="nav-link resume">RESUME 🔽</a>
                </Text>
            </Flex>
            <Box display={["block",null,"none"]}>
            
            </Box>
        </Flex>
    );
}

export default NavBar;
